import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router'
import { Avatar } from 'components'

import { Dropdown, Menu, Icon } from 'antd'
import { logout } from 'redux/modules/auth'

import './Header.scss'

@connect(
    state => ({auth: state.auth, routing: state.routing}),
    {logout}
)
export default class Header extends Component {

    handleLogout() {
        this.props.logout()
    }

    renderUserInfo() {
        const { auth } = this.props
        const userinfo = {
            username: auth.user.username
        }

        const userMenu = (
            <Menu className="user-menu">
                <Menu.Item>
                    <a onClick={this.handleLogout.bind(this)}><Icon type="logout" /> 退出登录</a>
                </Menu.Item>
            </Menu>
        )

        return (
            <Dropdown overlay={userMenu}>
                <div className="userinfo">
                    <Avatar userinfo={userinfo} />
                </div>
            </Dropdown>
        )
    }

    renderNavMenu() {
        const { auth } = this.props
        const modules = (auth.user.modules || []).filter(item => item.showInHeader)
        const currentModule = modules.filter(item => item.href === window.location.pathname).map(item => item.module)

        return (
            <Menu className="nav-menu" mode="horizontal" selectedKeys={currentModule}>
                {modules.map(item =>
                    <Menu.Item key={item.module}>
                        <Link to={item.href}>{item.name}</Link>
                    </Menu.Item>
                )}
            </Menu>
        )
    }

    renderGithubIcon() {
        return (
            <svg className="navbar-nav-svg" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 499.36" focusable="false">
                <title>GitHub</title>
                <path d="M256 0C114.64 0 0 114.61 0 256c0 113.09 73.34 209 175.08 242.9 12.8 2.35 17.47-5.56 17.47-12.34 
                    0-6.08-.22-22.18-.35-43.54-71.2 15.49-86.2-34.34-86.2-34.34-11.64-29.57-28.42-37.45-28.42-37.45-23.27-15.84 
                    1.73-15.55 1.73-15.55 25.69 1.81 39.21 26.38 39.21 26.38 22.84 39.12 59.92 27.82 74.5 21.27 2.33-16.54 
                    8.94-27.82 16.25-34.22-56.84-6.43-116.6-28.43-116.6-126.49 0-27.95 10-50.8 26.35-68.69-2.63-6.48-11.42-32.5 
                    2.51-67.75 0 0 21.49-6.88 70.4 26.24a242.65 242.65 0 0 1 128.18 0c48.87-33.13 70.33-26.24 70.33-26.24 14 
                    35.25 5.18 61.27 2.55 67.75 16.41 17.9 26.31 40.75 26.31 68.69 0 98.35-59.85 120-116.88 126.32 9.19 7.9 17.38 
                    23.53 17.38 47.41 0 34.22-.31 61.83-.31 70.23 0 6.85 4.61 14.81 17.6 12.31C438.72 464.97 512 369.08 512 256.02 
                    512 114.62 397.37 0 256 0z"
                    fill="currentColor"
                    fill-rule="evenodd"
                >
                </path>
            </svg>
        )
    }

    render() {
        const { auth, onLogin } = this.props

        return (
            <div className="header">
                <div className="container">
                    <a className="logo" href="/">Place</a>
                    { auth.user && auth.user.level && this.renderNavMenu() }
                    {
                        // auth.user && auth.user.level ? this.renderUserInfo() : 
                        // <div className="userinfo">
                        // <a className="link-login" onClick={onLogin}>登录</a>
                        // </div>
                    }
                    <a className="header-icon-github" target="_blank" href="https://github.com/4074/DrawingPlace">
                        {this.renderGithubIcon()}
                    </a>
                </div>
            </div>
        )
    }
}


